    componentDidMount() {

        this.handleLoadSourceDestJointTable(this);

        var jsonIndustryList = [
            { IndustryId: 1, Industry: "Pharamacy", bgColor: "info-box-icon bg-info", icon: "far fa-envelope" },
            { IndustryId: 2, Industry: "Business", bgColor: "info-box-icon bg-warning", icon: "far fa-star" },
            { IndustryId: 3, Industry: "Education", bgColor: "info-box-icon bg-danger", icon: "far fa-copy" },
            { IndustryId: 4, Industry: "Sales", bgColor: "info-box-icon bg-success", icon: "far fa-flag" },
            { IndustryId: 5, Industry: "ITS", bgColor: "info-box-icon bg-danger", icon: "far fa-star" },
        ];
        this.setState({
            IndustryList: jsonIndustryList
        });


        // Get Available Files


        this.GetFileObjectList();

    }

    GetFileObjectList() {
        this.setState({ AvailableFiles: [] });

        fetch(
            "http://localhost:8080/api/fileObjectsGetAllFiles?userId=2"
        )
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    isLoaded: true,
                    AvailableFiles: json,
                });
            });


    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })

        debugger;
    }

    handleAddProjectFiles = (obj) => {
        var jsonBase = this.state.ProjectFiles;

        jsonBase.push({
            id: obj._dispatchInstances.key,
            Title: obj._targetInst.memoizedProps.title
        });



        this.setState({
            ProjectFiles: jsonBase
        });
    }


    handleFileManagement = (obj) => {
        this.setState({
            ProjectTabClass: "tab-pane fade",
            FileManagementTabClass: "tab-pane fade show active",
            ProjectTabHeaderClass: "nav-link",
            FileManagementTabHeaderClass: "nav-link active"
        });
    }

