    handleSaveNewTable = (obj) => {

        var newJson = [];

        newJson.push({
            key: this.state.newTableInput.replace(" ", ""),
            items: this.state.JointTableStep1[0].items.concat(this.state.JointTableStep1[1].items)
        });



        this.setState({
            JoinTabelSourceList: this.state.JoinTabelSourceList.concat(newJson),
            JoinTabelDestinationList: this.state.JoinTabelDestinationList.concat(newJson),
            TableFeatureListStep0: this.state.JoinTabelDestinationList.concat(newJson),

            SourceFeature: [],
            DestinationFeature: [],

            JointTableStep1: [],
            JointTableSourceStep0: [],
            JointTableDestinationStep0: []
        })

        debugger;
    }

    handleCascadeSourceTableFeature = (e) => {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            var source = [...this.state.TableFeatureListStep0];

            this.setState({
                SourceFeature: source.filter
                    (x => x.key === this.state.PkFkrelationFromDrp)
            })

        });
    }


    handleCascadeDestTableFeature = (e) => {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            var dest = [...this.state.TableFeatureListStep0];

            this.setState({
                DestinationFeature: dest.filter
                    (x => x.key === this.state.PkFkrelationToDrp)
            })

        });
    }




