    handleBindProjects = (obj) => {

        if (this.state.IndustryOption === 0) {
            this.setState({
                AlertVisible: true
            });
        }
        else {
            var jsonIndProjectList = [
                { IndustryId: 2, Project: "Business Project 1", ProjectId: 1 },
                { IndustryId: 2, Project: "Business Project 2", ProjectId: 2 },
                { IndustryId: 4, Project: "Sales Project 2", ProjectId: 3 },
            ];

            this.setState({
                //IndProjectList: jsonIndProjectList,
                AlertVisible: false,
                IndustryTabClass: "tab-pane fade",
                ProjectTabClass: "tab-pane fade show active",
                IndustryTabHeaderClass: "nav-link",
                ProjectTabHeaderClass: "nav-link active"
            });



            var baseJson = jsonIndProjectList;
            var newJson = [];
            var IndustryOptionVar = this.state.IndustryOption;

            Object.keys(baseJson).forEach(function (key) {

                if (baseJson[key].IndustryId == IndustryOptionVar) {
                    debugger;
                    newJson.push(baseJson[key]);
                }
            });


            this.setState({
                IndProjectList: newJson
            });


        }


    }



