import React, { Component } from 'react';

class MergeTables extends Component {
    state = {}


    render() {
        return (
            <div class="form-group col-md-12">
                <div class="info-box bg-danger" style={{ display: this.props.isMergeTableError ? 'block' : 'none' }}>
                    <div class="info-box-content">
                        <span class="info-box-text">{this.props.mergeTableErrorMessage}</span>
                    </div>
                </div>
                Table Name: <input type="text" class="form-control" name="fnewTableNameInp"
                    onChange={this.props.handleChange}
                />

                <button type="button" class="btn btn-info btn-flat"
                //onClick={this.props.onUploadFile}
                >
                    Merge Tables
                            </button>
            </div>

        );
    }
}

export default MergeTables;