import React, { Component } from 'react';


class IndProject extends Component {
    state = {}
    render() {
        return (<div className="row">

            <div className="col-md-12">
                <table id="example1" className="table table-bordered table-striped">
                    <thead>
                        <tr>
                            <th></th>
                            <th>Project</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.props.ProjectList.map((Item) => (
                            <tr>
                                <td>
                                    <input type="radio" key={Item.ProjectId} value="option1"
                                    />

                                </td>
                                <td>
                                    {Item.Project}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <div className="col-md-12" >
                <button type="submit" class="btn btn-info"
                    onClick={this.props.onBindFileManagement}
                >Go to File Management</button>
                <br /><br />
            </div>
            <div className="col-md-12">
                <div class="card card-info">
                    <div class="card-header">
                        <h3 class="card-title">Create Project</h3>
                    </div>
                    <div class="card-body">
                        <div class="form-group row">
                            <label for="inputEmail3" class="col-sm-2 col-form-label">Project</label>
                            <div class="col-sm-10">
                                <input type="text" class="form-control" id="inputEmail3" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </div>);
    }
}

export default IndProject;