import React, { Component } from 'react';
import UVChart3 from './graph/uvchart3';
import UVChart4 from './graph/uvchart4';

class UnitVarience extends Component {
    state = {}
    render() {
        return (
            <div>

                <div class="row">
                    <div class="col-sm-12">
                        <div class="form-group">
                            <label>Table</label>
                            <select className="form-control"
                                name="UnitVarienceTableDrp"
                                onChange={this.props.onOptionVarienceTableChange}
                            >
                                {
                                    this.props.UnitVarienceTableList.map(Item => (
                                        <option value={Item.id}>{Item.key}</option>
                                    ))

                                }
                            </select>
                        </div>
                    </div>
                    <div class="col-sm-12">
                        <div class="form-group">
                            <div class="row">
                                {
                                    this.props.UnitVarienceFeatureList.map(Item => (
                                        Item.items.map(subItem =>
                                            <div class="custom-control custom-radio col-sm-3">
                                                <input class="custom-control-input"
                                                    type="radio" id={"n" + subItem.id} name="customRadio" />
                                                <label for={"n" + subItem.id} class="custom-control-label">{subItem.name}</label>
                                            </div>
                                        )

                                    ))
                                }
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-12">
                        <UVChart3 />
                        <UVChart4 />
                    </div>
                </div>



            </div>

        );
    }
}

export default UnitVarience;