import React, { Component } from 'react';
import Header from '../../Header';
import Menu from '../../Menu';
import * as ROUTES from '../../constants/routes';

import {
    BrowserRouter as Router,
    Route,
} from 'react-router-dom';

import HomePage from '../home';
// import Landing from '../landing';
import Signup from '../signup';
import Setup from '../setup';
import PrepareData from '../preparedata';
import SignInPage from '../signin';
import { withFirebase } from '../firebase';
import FileManager from '../filemanager';
import DatabaseDiagram from '../databasediagram';



class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            authUser: null,
        };
    }

    componentDidMount() {
        this.listener = this.props.firebase.auth.onAuthStateChanged(authUser => {
            authUser
                ? this.setState({ authUser })
                : this.setState({ authUser: null });
        });
    }

    componentWillUnmount() {
        this.listener();
    }

    render() {
        return (
            <div>
                <Header />
                <Router>
                    <Menu authUser={this.state.authUser} />
                    <div className="content-wrapper">
                        <section className="content-header">
                            <div className="container-fluid">
                                <div className="row mb-2">
                                    <div className="col-sm-6">
                                        {/* <h1>DataTables</h1> */}
                                    </div>
                                    <div className="col-sm-6">
                                        <ol className="breadcrumb float-sm-right">
                                            {/* <li className="breadcrumb-item"><a href="fake_url">Home</a></li>
                                    <li className="breadcrumb-item active">DataTables</li> */}
                                        </ol>
                                    </div>
                                </div>
                            </div>{/* /.container-fluid */}
                        </section>
                        {/* Main content */}
                        <section className="content">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-12">
                                        <Route path={ROUTES.HOME} component={HomePage} />
                                        {/* <Route path={ROUTES.LANDING} component={Landing} /> */}
                                        <Route path={ROUTES.Setup} component={Setup} />
                                        <Route path={ROUTES.Preparing_Data} component={PrepareData} />
                                        <Route path={ROUTES.SIGN_UP} component={Signup} />
                                        <Route path={ROUTES.SIGN_IN} component={SignInPage} />
                                        <Route path={ROUTES.File_Manager} component={FileManager} />
                                        <Route path={ROUTES.Database_Diagram} component={DatabaseDiagram} />
                                    </div>
                                    {/* /.col */}
                                </div>
                                {/* /.row */}
                            </div>
                            {/* /.container-fluid */}
                        </section>
                        {/* /.content */}

                    </div>

                </Router>
            </div>

        );
    }
}

export default withFirebase(App);

