import { Column, DataGrid, Paging } from 'devextreme-react/data-grid';
import axio from 'axios';
import React, { Component } from 'react';

class TopNRow extends Component {
    state = {}
    // constructor(props) {
    //     super(props);
    //     this.state = {

    //     };
    // }
    render() {
        return (
            <DataGrid
                dataSource={this.state.topNRow}
                showBorders={true}
            >
                <Paging pageSize={10} />


            </DataGrid>
        );
    }

    async componentDidMount() {
        debugger;
        const postObject = {
            file_id: this.props.fileId,
            page_no: 1,
            page_size: 100,
            order_by: "-1"
        };
        await axio.post("http://brightbee.giize.com:8000/api/files/show_records/", postObject)
            .then(res => {
                this.setState({
                    topNRow: res.data
                })
            })
    }

}

export default TopNRow;