import React, { Component } from 'react';

class Table1 extends Component {
    constructor(props){
        super(props);

        this.state = {
            Items: [],
            isLoaded: false
        }
    }

    componentDidMount(){
        fetch('http://tevw482.mtnirancell.ir:4010/api/SharepointDocLib/GetAll?url=http://tevw485.mtnirancell.ir/sites/its/IE/')
        .then(res => res.json())
        .then(json =>{
            this.setState({
                isLoaded: true,
                Items: json,
            })
        });
    }

    render() { 
        var {isLoaded, Items} = this.state;

        console.log("isLoaded: " + isLoaded);

        if (!isLoaded){
            return <div>Loading ...</div>;
        }

        else{
            return ( 
                <table id="example1" className="table table-bordered table-striped">
                                <thead>
                                <tr>
                                    <th>Title</th>
                                    <th>Entity Type Name</th>
                                    <th>Count</th>
                                    <th>is Hidden</th>
                                    <th>Size</th>
                                </tr>
                                </thead>
                                <tbody>

                                    {
                                        Items.map(Item =>(
                                            <tr>
                                                <td>
                                                    {Item.title}
                                                </td>
                                                <td>
                                                    {Item.entityTypeName}
                                                </td>
                                                <td>
                                                    {Item.count}
                                                </td>
                                                <td>
                                                    {Item.isHidden}
                                                </td>
                                                <td>
                                                    {Item.size}
                                                </td>
                                            </tr>
                                        ))}
                                
                                </tbody>
                            </table>
                            
                );
        }

        
    }
}
 
export default Table1;