import React, { Component } from 'react';

class ProjectFile extends Component {
    state = {  }
    render() { 
        return ( <table id="example1" className="table table-bordered table-striped">
        <thead>
            <tr>
                <th>Title</th>
            </tr>
        </thead>
        <tbody>
                {this.props.ProjectFiles_.map((Item) => (
                    <tr>
                        
                        <td>
                            {Item.Title}
                        </td>
                    </tr>
                ))}
            </tbody>
    </table> );
    }
}
 
export default ProjectFile;