import React, { Component } from 'react';

class Industry extends Component {
    state = {}
    render() {

        // var { IndustryList } = this.state.IndustryList;
        return (
            <div>
            {this.props.AlertVisible && 
                <div className="row">
                    <div className="col-md-12">
                        <div class="alert alert-danger alert-dismissible" id="industryAlert">
                            You have to choose one of industries !
                        </div>
                    </div>
                </div>
            }

                <div className="row">
                    

                    {
                        this.props.IndustryList_.map(Item => (
                            React.createElement('div', { className: 'col-md-3 col-sm-6 col-12' },
                                React.createElement('div', {
                                    className: 'info-box',
                                    //onClick: this.props.onBindProjects,
                                    key: Item.IndustryId
                                },
                                    React.createElement('span', { className: Item.bgColor },
                                        React.createElement('i', { className: Item.icon }
                                        )

                                    )
                                    ,
                                    React.createElement('div', { className: 'info-box-content' },
                                        React.createElement('input', {
                                            type: 'radio', name: 'IndustryOption', value:Item.IndustryId,
                                            onChange:this.props.onOptionChange

                                        }),
                                        React.createElement('span', { className: 'info-box-text' }, Item.Industry),

                                    )
                                )
                            )))
                    }


                </div>
                <div className="row">
                    <div className="col-md-10"></div>
                    <div className="col-md-2" ><button type="submit" class="btn btn-info"
                        onClick={this.props.onBindProjects}
                    >Choose Projects</button></div>
                </div>
            </div>
        );
    }
}

export default Industry;