import React, { Component } from 'react';
import * as go from 'gojs';
import { ReactDiagram } from 'gojs-react';
import '../../../App.css';
import TableFeatureDropDown from '../common/TableFeaturesDropDown';
import RelationTable from './relationTable';


class DbDiagram extends Component {
    state = {}


    render() {
        return (

            <div >

                <div className="row">
                    <div class="col-md-3" >
                        {

                            this.props.DiagramLeftTableItems.map(Item => (
                                React.createElement('div', { className: 'card  collapsed-card' },
                                    React.createElement('div', { className: 'card-header' },
                                        React.createElement('h3', { className: Item.active ? 'card-title' : 'card-title grayedOutText' }, Item.key),
                                        React.createElement('div', { className: 'card-tools' },
                                            Item.active && React.createElement('button', { type: 'button', class: 'btn btn-tool', 'data-card-widget': 'collapse' },
                                                React.createElement('i', { class: 'fas fa-plus' })
                                            ),
                                            !!!Item.active && React.createElement('i', {
                                                class: 'fa fa-undo',
                                                key: Item.id,
                                                onClick: this.props.handleActiveTable.bind(this)
                                            })

                                        )

                                    ),
                                    React.createElement('div', { className: 'card-body p-0 displayNone ' },
                                        React.createElement('ul', { className: 'nav nav-pills flex-column' },
                                            Item.items.map(subItem =>

                                                React.createElement('li',
                                                    {
                                                        Parent: Item.key,
                                                        IsDeleted: subItem.isDeleted,
                                                        key: subItem.id, onClick: this.props.handleDeleteFields.bind(this),

                                                        href: '#', className: subItem.isDeleted ? 'nav-link Decor-line' : 'nav-link'
                                                    }, subItem.name),

                                            )
                                        )
                                    )
                                )))
                        }


                    </div>

                    <div class="col-md-9" >
                        <ReactDiagram class="diagram-cover"
                            initDiagram={initDiagram}
                            divClassName='diagram-component'
                            nodeDataArray={this.props.DiagramItems}
                            linkDataArray={this.props.RelationDiagramItems}
                            onModelChange={handleModelChange}
                        />


                        <div className="card">
                            <div className="card-header">
                                <h3 className="card-title">Relational Diagram</h3>
                            </div>
                            {/* /.card-header */}
                            <div className="card-body">
                                <TableFeatureDropDown
                                    TableFeatureListSrc={this.props.TableFeatureListSrc}
                                    SourceFeature={this.props.SourceFeature}
                                    TableFeatureListDest={this.props.TableFeatureListDest}
                                    DestinationFeature={this.props.DestinationFeature}
                                    onOptionChange={this.props.onOptionChange}
                                    onOptionSourceChange={this.props.onOptionSourceChange}
                                    onOptionDestChange={this.props.onOptionDestChange}
                                />

                                <div className="row">

                                    <div className="col-md-6">
                                        <div class="form-group">
                                            <button type="submit" class="btn btn-info"
                                                onClick={this.props.onBindCreateNewTableRelation}

                                            >Create Relations</button>
 -
                                            <button type="submit" class="btn btn-secondary"
                                            //onClick={this.props.onBindClearTable}

                                            >Clear</button>
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-md-12">
                                        <RelationTable
                                            RelationDiagramItems={this.props.RelationDiagramItems}
                                            handleChange={this.props.handleChange}
                                            onCreateMerge={this.props.onCreateMerge}
                                            MergeTableModal={this.props.MergeTableModal}
                                            showMergeModal={this.props.showMergeModal}
                                            isMergeModalError={this.props.isMergeModalError}
                                            mergeModalErrorMessage={this.props.mergeModalErrorMessage}
                                        />
                                    </div>
                                </div>
                            </div>
                            {/* /.card-body */}
                        </div>



                    </div>
                </div>



            </div>
        );
    }
}


function initDiagram() {
    const $ = go.GraphObject.make;
    // set your license key here before creating the diagram: go.Diagram.licenseKey = "...";

    var itemTempl =
        $(go.Panel, "Horizontal",
            $(go.Shape,
                { desiredSize: new go.Size(15, 15), strokeJoin: "round", strokeWidth: 3, stroke: null, margin: 2 },
                new go.Binding("figure", "figure"),
                new go.Binding("fill", "color"),
                new go.Binding("stroke", "color")),
            $(go.TextBlock,
                {
                    stroke: "#333333",
                    font: "bold 14px sans-serif"
                },
                new go.Binding("text", "name"))
        );
    const diagram =
        $(go.Diagram,
            {
                'allowHorizontalScroll': true,
                'undoManager.isEnabled': true,  // must be set to allow for model change listening
                // 'undoManager.maxHistoryLength': 0,  // uncomment disable undo/redo functionality
                'clickCreatingTool.archetypeNodeData': { text: 'new node', color: 'lightblue' },
                model: $(go.GraphLinksModel,
                    {
                        linkKeyProperty: 'key'  // IMPORTANT! must be defined for merges and data sync when using GraphLinksModel
                    })
            });


    // define a simple Node template
    diagram.nodeTemplate =
        $(go.Node, "Auto",  // the whole node panel
            {
                selectionAdorned: true,
                resizable: true,
                layoutConditions: go.Part.LayoutStandard & ~go.Part.LayoutNodeSized,
                fromSpot: go.Spot.AllSides,
                toSpot: go.Spot.AllSides,
                isShadowed: true,
                shadowOffset: new go.Point(3, 3),
                shadowColor: "#C5C1AA"
            },
            new go.Binding("location", "location").makeTwoWay(),
            // whenever the PanelExpanderButton changes the visible property of the "LIST" panel,
            // clear out any desiredSize set by the ResizingTool.
            new go.Binding("desiredSize", "visible", function (v) { return new go.Size(NaN, NaN); }).ofObject("LIST"),
            // define the node's outer shape, which will surround the Table
            $(go.Shape, "RoundedRectangle",
                { fill: 'white', stroke: "#eeeeee", strokeWidth: 3 }),
            $(go.Panel, "Table",
                { margin: 8, stretch: go.GraphObject.Fill },
                $(go.RowColumnDefinition, { row: 0, sizing: go.RowColumnDefinition.None }),
                // the table header
                $(go.TextBlock,
                    {
                        row: 0, alignment: go.Spot.Center,
                        margin: new go.Margin(0, 24, 0, 2),  // leave room for Button
                        font: "bold 16px sans-serif"
                    },
                    new go.Binding("text", "key")),
                // the collapse/expand button
                $("PanelExpanderButton", "LIST",  // the name of the element whose visibility this button toggles
                    { row: 0, alignment: go.Spot.TopRight }),
                // the list of Panels, each showing an attribute
                $(go.Panel, "Vertical",
                    {
                        name: "LIST",
                        row: 1,
                        padding: 3,
                        alignment: go.Spot.TopLeft,
                        defaultAlignment: go.Spot.Left,
                        stretch: go.GraphObject.Horizontal,
                        itemTemplate: itemTempl
                    },
                    new go.Binding("itemArray", "items"))
            )  // end Table Panel
        );

    return diagram;
}

function handleModelChange(changes) {
    // alert('GoJS model changed!');
}

export default DbDiagram;