import React  from 'react';
import { Link } from 'react-router-dom';
import * as ROUTES from './constants/routes';
import SignOutButton from './components/signout';


const Navigation = ({ authUser }) => (
    <div>{authUser ? <NavigationAuth /> : <NavigationNonAuth />}</div>
  );




  const NavigationAuth = () => (
      <aside className="main-sidebar sidebar-dark-primary elevation-4">
          {/* Brand Logo */}
          <a href="index3.html" className="brand-link">
              <img src="dist/img/AdminLTELogo.png" alt="AdminLTE Logo"
                  className="brand-image img-circle elevation-3" style={{ opacity: '.8' }} />
              <span className="brand-text font-weight-light">AdminLTE 3</span>
          </a>
          {/* Sidebar */}
          <div className="sidebar">
              {/* Sidebar user panel (optional) */}
              <div className="user-panel mt-3 pb-3 mb-3 d-flex">
                  <div className="image">
                      <img src="dist/img/user2-160x160.jpg"
                          className="img-circle elevation-2" alt="User_Image" />
                  </div>
                  <div className="info">
                      <a href="fake_url" className="d-block">Alexander Pierce</a>
                  </div>
              </div>
              {/* Sidebar Menu */}
              <nav className="mt-2">
                  <ul className="nav nav-pills nav-sidebar flex-column"
                      data-widget="treeview" role="menu" data-accordion="false">
                      {/* Add icons to the links using the .nav-icon class
        with font-awesome or any other icon font library */}
                      <li className="nav-item has-treeview menu-open">
                          <a href="fake_url" className="nav-link active">
                              <i className="nav-icon" />
                              <p>
                                  Section 1
            <i className="right fas fa-angle-left" />
                              </p>
                          </a>



                          <ul>
                              <li>
                                  <Link to={ROUTES.LANDING}>Landing</Link>
                              </li>
                              <li>
                                  <Link to={ROUTES.HOME}>Home</Link>
                              </li>
                              <li>
                                  <Link to={ROUTES.ACCOUNT}>Account</Link>
                              </li>
                              <li>
                                  <SignOutButton />
                              </li>
                          </ul>

                      </li>

                  </ul>
              </nav>
              {/* /.sidebar-menu */}
          </div>
          {/* /.sidebar */}
      </aside>
  );
   
  const NavigationNonAuth = () => (

      <aside className="main-sidebar sidebar-dark-primary elevation-4">
          {/* Brand Logo */}
          <a href="index3.html" className="brand-link">
              <img src="dist/img/AdminLTELogo.png" alt="AdminLTE Logo"
                  className="brand-image img-circle elevation-3" style={{ opacity: '.8' }} />
              <span className="brand-text font-weight-light">AdminLTE 3</span>
          </a>
          {/* Sidebar */}
          <div className="sidebar">
              {/* Sidebar user panel (optional) */}
              <div className="user-panel mt-3 pb-3 mb-3 d-flex">
                  <div className="image">
                      <img src="dist/img/user2-160x160.jpg"
                          className="img-circle elevation-2" alt="User_Image" />
                  </div>
                  <div className="info">
                      <a href="fake_url" className="d-block">Alexander Pierce</a>
                  </div>
              </div>
              {/* Sidebar Menu */}
              <nav className="mt-2">
                  <ul className="nav nav-pills nav-sidebar flex-column"
                      data-widget="treeview" role="menu" data-accordion="false">
                      {/* Add icons to the links using the .nav-icon class
        with font-awesome or any other icon font library */}
                      <li className="nav-item has-treeview menu-open">
                          <a href="fake_url" className="nav-link active">
                              <i className="nav-icon" />
                              <p>
                                  Section 1
            <i className="right fas fa-angle-left" />
                              </p>
                          </a>

                          <ul>
                              <li>
                                  <Link to={ROUTES.LANDING}>Landing</Link>
                              </li>
                              <li>
                                  <Link to={ROUTES.SIGN_IN}>Sign In</Link>
                              </li>
                          </ul>

                      </li>

                  </ul>
              </nav>
              {/* /.sidebar-menu */}
          </div>
          {/* /.sidebar */}
      </aside>
  );

 
export default Navigation;