import React, { Component } from 'react';
import DataGrid,
{
    Column, Editing,
    MasterDetail
} from 'devextreme-react/data-grid';

import FileDetails from "./fileDetails";
import axio from 'axios';


class Files extends Component {
    constructor(props) {
        super();
        this.onRowRemoving = this.DeleteFiles.bind(this);
    }

    DeleteFiles(obj) {
        axio.delete("http://brightbee.mywire.org:8000/api/files/" + obj.data.id + "/");
    }

    state = {}
    render() {
        return (
            <div>
                <div class="box-body">
                    <div class="form-group col-md-12">
                        <div class="info-box bg-danger" style={{ display: this.props.isFileError ? 'block' : 'none' }}>
                            <div class="info-box-content">
                                <span class="info-box-text">{this.props.fileErrorMessage}</span>
                            </div>
                        </div>
                        File Name: <input type="text" class="form-control" name="fileNameInp" onChange={this.props.handleChange} />
                                File Path: <input type="text" class="form-control" name="filePathInp" onChange={this.props.handleChange} />
                        <button type="button" class="btn btn-info btn-flat"
                            onClick={this.props.onUploadFile}
                        >
                            Create File
                            </button>
                    </div>

                </div>



                <DataGrid id="grid-container"
                    dataSource={this.props.fileList}
                    keyExpr="id"
                    showBorders={true}
                    onRowRemoved={this.onRowRemoved}
                    onRowRemoving={this.onRowRemoving}

                >
                    <Editing
                        mode="row"
                        useIcons={true}

                        allowDeleting={true} />

                    <Column dataField="key" caption="Title" />
                    <Column dataField="size" />
                    <Column dataField="date_created" dataType="date" />
                    <Column type="buttons" width={110}
                        buttons={['delete']} />
                    <MasterDetail
                        enabled={true}
                        component={FileDetails}

                    />
                </DataGrid>

            </div>
        );
    }
}

export default Files;
