import React, { Component } from 'react';
import { Column, DataGrid, Paging } from 'devextreme-react/data-grid';
import axio from 'axios';

class FeatureType extends Component {
    //state = {}
    constructor(props) {
        super(props);
        this.state = {
            featureTypeList: null
        };
    }
    render() {
        return (
            <DataGrid
                dataSource={this.state.featureTypeList}
                showBorders={true}
            >
                <Paging pageSize={10} />
                <Column dataField="name" Title="Field name" />
                <Column dataField="field_type" Title="Field Type" />
                <Column dataField="isDeleted" Title="Is Deleted" />

            </DataGrid>
        );
    }

    async componentDidMount() {
        const postObject = {
            user_id: 111,
            file_id: this.props.fileId
        };
        await axio.post("http://brightbee.giize.com:8000/api/fields/details/", postObject)
            .then(res => {
                this.setState({
                    featureTypeList: res.data
                })
            })
    }
}

export default FeatureType;