import React, { Component } from 'react';
import TableFeatureDropDown from '../common/TableFeaturesDropDown';

class TableRelations extends Component {
    state = {}
    render() {
        return (<div>
            <div className="row">
                <div className="col-md-12">
                    <div class="card card-info">
                        <div class="card-header">
                            <h3 class="card-title">Create Joint Table</h3>
                        </div>
                        <div class="card-body">

                            <TableFeatureDropDown
                                TableFeatureListSrc={this.props.TableFeatureListSrc}
                                SourceFeature={this.props.SourceFeature}
                                TableFeatureListDest={this.props.TableFeatureListDest}
                                DestinationFeature={this.props.DestinationFeature}
                                onOptionChange={this.props.onOptionChange}
                                onOptionSourceChange={this.props.onOptionSourceChange}
                                onOptionDestChange={this.props.onOptionDestChange}
                            />


                            <div className="row">

                                <div className="col-md-6">
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-info"
                                            onClick={this.props.onBindJoinTable}

                                        >Make Joint Table</button>
                                 -
                                <button type="submit" class="btn btn-secondary"
                                            onClick={this.props.onBindClearTable}

                                        >Clear</button>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>

            </div>
            <div className="row">

                <div className="col-md-6">
                    <div class="card">
                        <div class="card-header">
                            <h5 class="card-title m-0">Source Tables</h5>
                        </div>
                        <div class="card-body">
                            {
                                this.props.JointTableSourceStep0.map(Item => (
                                    React.createElement('div', { className: 'card' },
                                        React.createElement('div', { className: 'card-header' },
                                            React.createElement('h3', { className: 'card-title' }, Item.key),
                                            React.createElement('div', { className: 'card-tools' }

                                            )

                                        ),
                                        React.createElement('div', { className: 'card-body p-0' },
                                            React.createElement('ul', { className: 'nav nav-pills flex-column' },
                                                Item.items.map(subItem =>

                                                    React.createElement('li',
                                                        {
                                                            className: 'nav-link'
                                                        }, subItem.name + ' ',


                                                        React.createElement('small',
                                                            { className: 'badge badge-success' },

                                                            subItem.iskey ? 'Key' : ''
                                                        )
                                                    ),

                                                )
                                            )
                                        )
                                    )))
                            }

                            {
                                this.props.JointTableDestinationStep0.map(Item => (
                                    React.createElement('div', { className: 'card' },
                                        React.createElement('div', { className: 'card-header' },
                                            React.createElement('h3', { className: 'card-title' }, Item.key),
                                            React.createElement('div', { className: 'card-tools' }

                                            )

                                        ),
                                        React.createElement('div', { className: 'card-body p-0' },
                                            React.createElement('ul', { className: 'nav nav-pills flex-column' },
                                                Item.items.map(subItem =>

                                                    React.createElement('li',
                                                        {
                                                            className: 'nav-link'
                                                        }, subItem.name + ' ',


                                                        React.createElement('small',
                                                            { className: 'badge badge-success' },

                                                            subItem.iskey ? 'Key' : ''
                                                        )
                                                    ),

                                                )
                                            )
                                        )
                                    )))
                            }

                        </div>
                    </div>
                </div>
                <div className="col-md-6">
                    <div class="card">
                        <div class="card-header">
                            <h5 class="card-title m-0">Joint Table</h5>
                        </div>
                        <div class="card-body">
                            {
                                React.createElement('div', { className: 'card' },
                                    React.createElement('div', { className: 'card-body p-0' },
                                        React.createElement('ul', { className: 'nav nav-pills flex-column' },
                                            this.props.JointTableStep1.map(Item => (

                                                Item.items.map(subItem =>

                                                    React.createElement('li',
                                                        {
                                                            className: 'nav-link'
                                                        }, Item.key + '.' + subItem.name + ' ',


                                                        React.createElement('small',
                                                            { className: 'badge badge-success' },

                                                            subItem.iskey ? 'Key' : ''
                                                        )
                                                    ),

                                                )
                                            )
                                            )
                                        )))
                            }


                        </div>
                    </div>
                </div>


            </div>
            <div className="row">

                <div className="col-md-12">
                    <div class="form-group row">
                        <label for="inputEmail3" class="col-sm-2 col-form-label">Table Name</label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" name="newTableInput"
                                onChange={this.props.onOptionChange}
                            />

                        </div>
                        <button className="btn btn-info col-sm-2"
                            onClick={this.props.onSaveNewTable}
                        >Save</button>
                    </div>
                </div>

            </div>



        </div>);
    }
}

export default TableRelations;