import React, { Component } from 'react';
import { Column, DataGrid, Paging, MasterDetail, Button } from 'devextreme-react/data-grid';
import '../common/custom.css';
import MergeTables from './mergeTables';
import { Modal } from 'react-bootstrap';

class RelationTable extends Component {
    state = {
        show: false,
        relationObj: []
    }

    constructor(props) {
        super(props);
        debugger;
        this.state = {
            RelationDiagramItemsState: this.props.RelationDiagramItems
        };
    }


    handleClose = () => {
        this.setState({
            show: false
        })
    }
    render() {


        return (
            <div>
                <Modal show={this.props.showMergeModal} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Merge Table</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div class="form-group col-md-12">
                            <div class="info-box bg-danger" style={{ display: this.props.isMergeModalError ? 'block' : 'none' }}>
                                <div class="info-box-content">
                                    <span class="info-box-text">{this.props.mergeModalErrorMessage}</span>
                                </div>
                            </div>
                Table Name: <input type="text" class="form-control" name="newTableNameInp"
                                onChange={this.props.handleChange}
                            />

                            <button type="button" class="btn btn-info btn-flat"
                                onClick={this.props.onCreateMerge}
                            >
                                Merge Tables
                            </button>
                        </div>


                    </Modal.Body>
                </Modal>

                <DataGrid
                    dataSource={this.props.RelationDiagramItems}
                    showBorders={true}
                >
                    <Paging pageSize={10} />
                    <Column dataField="from" Title="From Table" />
                    <Column dataField="to" Title="To Table" />
                    <Column dataField="fromField" Title="From Field" />
                    <Column dataField="toField" Title="To Field" />


                    <Column type="buttons" width={110}
                        buttons={['edit', 'delete', {
                            hint: 'Clone',
                            icon: 'repeat',
                            //visible: this.isCloneIconVisible,
                            onClick: this.props.MergeTableModal
                        }]} />

                    <MasterDetail
                        enabled={true}
                        component={MergeTables}


                    />
                </DataGrid>

            </div>

        );
    }


}

export default RelationTable;