import React, { Component } from "react";
import * as Icons from '../../../constants/icons';
import * as fileExist from 'file-exists';

class FileList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Items: [],
      Items2: [],
      isLoaded: false,
      isLoaded2: false,
      currentId: 0,
      folderNameInp: '',
      fileNameInp: '',
      filePathInp: ''
    };
  }

  componentDidMount() {
    this.GetFileObjectList(0);
  }

  GetFileObjectList(currentId) {
    this.setState({ Items: [] });

    this.setState({ currentId: currentId });

    fetch(
      "http://localhost:8080/api/fileObjects?userId=2&parentId=" + currentId
    )
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          isLoaded: true,
          Items: json,
        });
      });

    this.GetGrandParentId(currentId);
  }

  GetGrandParentId(id) {
    fetch("http://localhost:8080/api/fileObjectsFindParent?id=" + id)
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          isLoaded2: true,
          Items2: json,
        });
      });
  }



  CreateNewFolder() {

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        title: (this.state.folderNameInp),
        "objectType": "Folder",
        "filePath": "None",
        "userId": 2,
        "parentId": this.state.currentId
      })
    };
    fetch('http://localhost:8080/api/fileObjects', requestOptions)
      .then(response => response.json())
      .then(() => this.GetFileObjectList(this.state.currentId));


  }


  CreateNewFile() {


    debugger;


    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        title: (this.state.fileNameInp),
        "objectType": "File",
        "filePath": (this.state.filePathInp),
        "userId": 2,
        "parentId": this.state.currentId
      })
    };
    fetch('http://localhost:8080/api/fileObjects', requestOptions)
      .then(response => response.json())
      .then(() => this.GetFileObjectList(this.state.currentId));


  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  render() {
    var { isLoaded, Items, Items2 } = this.state;

    console.log("isLoaded: " + isLoaded);

    if (!isLoaded) {
      return <div>Loading ...</div>;
    }

    // if (this.prevPage !== 0){
    //     const prevPageIcon =  <tr><td></td><td>{this.prevPage}</td></tr>
    // }

    return (
      <div>
        <form>

          <div class="box-body">
            <div class="form-group col-md-12">
              <div class="input-group input-group-sm">
                <input type="text" class="form-control" name="folderNameInp" onChange={this.handleChange} />
                <span class="input-group-btn">
                  <button type="button" class="btn btn-info btn-flat"
                    onClick={() => this.CreateNewFolder()}
                  >
                    Create Folder
                        </button>
                </span>
              </div>
            </div>

            <div class="form-group col-md-12">
              File Name: <input type="text" class="form-control" name="fileNameInp" onChange={this.handleChange} />
                    File Path: <input type="text" class="form-control" name="filePathInp" onChange={this.handleChange} />
              <button type="button" class="btn btn-info btn-flat"
                onClick={() => this.CreateNewFile()}
              >
                Create File
                        </button>
            </div>
          </div>
        </form>

        <table id="example1" className="table table-bordered table-striped">
          <thead>
            <tr>
              <th></th>
              <th>Title</th>
              <td></td>
            </tr>
          </thead>
          <tbody>
            {Items2.map((Item) => (
              <tr>
                <td>
                  <a
                    onClick={() => this.GetFileObjectList(Item.parentId)}
                    href="#"
                  >
                    {returnUp()}
                  </a>
                </td>
                <td> </td>
              </tr>
            ))}

            {Items.map((Item) => (
              <tr>
                <td>
                {(() => {
                    switch (Item.objectType) {
                      case "File":
                        return <input  type="checkbox" key={Item.id} title={Item.filePath}  value="option1" 
                          onChange={this.props.onAddProjectFiles} />
                      default:
                        return ""
                    }
                  })()}
                </td>
                
                <td>


                  {(() => {
                    switch (Item.objectType) {
                      case "Folder":
                        return showFolder();
                      default:
                        return showFile();
                    }
                  })()}
                  &nbsp;
                  

                  {(() => {
                    switch (Item.objectType) {
                      case "File":
                        return <a href={Item.filePath}>{Item.title}</a>;
                      default:
                        return (
                          <a
                            onClick={() => this.GetFileObjectList(Item.id)}
                            href="#"
                          >
                            {Item.title}
                          </a>
                        );
                    }
                  })()}
                </td>
                <td>
                  {Icons.removeIcon}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );

    function showFolder() {
      return (
        <svg
          width="1em"
          height="1em"
          viewBox="0 0 16 16"
          class="bi bi-folder"
          fill="#FF5733"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M9.828 4a3 3 0 0 1-2.12-.879l-.83-.828A1 1 0 0 0 6.173 2H2.5a1 1 0 0 0-1 .981L1.546 4h-1L.5 3a2 2 0 0 1 2-2h3.672a2 2 0 0 1 1.414.586l.828.828A2 2 0 0 0 9.828 3v1z" />
          <path
            fill-rule="evenodd"
            d="M13.81 4H2.19a1 1 0 0 0-.996 1.09l.637 7a1 1 0 0 0 .995.91h10.348a1 1 0 0 0 .995-.91l.637-7A1 1 0 0 0 13.81 4zM2.19 3A2 2 0 0 0 .198 5.181l.637 7A2 2 0 0 0 2.826 14h10.348a2 2 0 0 0 1.991-1.819l.637-7A2 2 0 0 0 13.81 3H2.19z"
          />
        </svg>
      );
    }

    function showFile() {
      return (
        <svg
          width="1em"
          height="1em"
          viewBox="0 0 16 16"
          class="bi bi-file-text"
          fill="currentColor"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            d="M4 1h8a2 2 0 0 1 2 2v10a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2zm0 1a1 1 0 0 0-1 1v10a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H4z"
          />
          <path
            fill-rule="evenodd"
            d="M4.5 10.5A.5.5 0 0 1 5 10h3a.5.5 0 0 1 0 1H5a.5.5 0 0 1-.5-.5zm0-2A.5.5 0 0 1 5 8h6a.5.5 0 0 1 0 1H5a.5.5 0 0 1-.5-.5zm0-2A.5.5 0 0 1 5 6h6a.5.5 0 0 1 0 1H5a.5.5 0 0 1-.5-.5zm0-2A.5.5 0 0 1 5 4h6a.5.5 0 0 1 0 1H5a.5.5 0 0 1-.5-.5z"
          />
        </svg>
      );
    }

    function returnUp() {
      return (
        <svg
          width="1em"
          height="1em"
          viewBox="0 0 16 16"
          class="bi bi-arrow-bar-up"
          fill="#70685E"
          xmlns="http://www.w3.org/2000/svg"

        >
          <path
            fill-rule="evenodd"
            d="M11.354 5.854a.5.5 0 0 0 0-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L8 3.207l2.646 2.647a.5.5 0 0 0 .708 0z"
          />
          <path
            fill-rule="evenodd"
            d="M8 10a.5.5 0 0 0 .5-.5V3a.5.5 0 0 0-1 0v6.5a.5.5 0 0 0 .5.5zm-4.8 1.6c0-.22.18-.4.4-.4h8.8a.4.4 0 0 1 0 .8H3.6a.4.4 0 0 1-.4-.4z"
          />
        </svg>
      );
    }
  }
}

export default FileList;
