import React, { Component } from 'react';
import { TabPanel, Item } from 'devextreme-react/tab-panel';
import FeatureType from './featureType';
import TopNRow from './topNRow';

class FileDetails extends Component {

    constructor(props) {
        super(props);
        this.renderFeatureTypeTab = this.renderFeatureTypeTab.bind(this);
        this.renderTopNRowTab = this.renderTopNRowTab.bind(this);
    }

    state = {}
    render() {
        return (
            <TabPanel>
                <Item title="Feature Types" render={this.renderFeatureTypeTab} />
                <Item title="Top n Records" render={this.renderTopNRowTab} />
            </TabPanel>
        );
    }

    renderFeatureTypeTab() {
        return <FeatureType fileId={this.props.data.key} />;
    }

    renderTopNRowTab() {
        return <TopNRow fileId={this.props.data.key} />;
    }
}

export default FileDetails;