import React, { Component } from 'react';
import * as go from 'gojs';
import { ReactDiagram } from 'gojs-react';
import '../../App.css';
import * as Icons from '../../constants/icons';





class databasediagram extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Items: [],
      DiagramItems: [],
      RelationItems: [],
      RelationDiagramItems: [],
      relationFromDrp: '',
      relationToDrp: '',
      colors: {}
    }

  }





  colors = {
    'red': '#be4b15',
    'green': '#52ce60',
    'blue': '#6ea5f8',
    'lightred': '#fd8852',
    'lightblue': '#afd4fe',
    'lightgreen': '#b9e986',
    'pink': '#faadc1',
    'purple': '#d689ff',
    'orange': '#fdb400',
  }

  handleDeleteFields = (obj) => {

    debugger;
    this.BindLeftTableById(obj._targetInst.key, this);
    this.BindDiagramById(obj._targetInst.key, this);
    this.BindRelationById(obj._targetInst.memoizedProps.Parent,
      obj._targetInst.memoizedProps.children,
      obj._targetInst.memoizedProps.IsDeleted,
      this
    )

  }


  BindDiagram = (id, _Items, _this) => {

    var baseJson = _Items;
    var newJson = [];
    var baseJsonKeys = [];


    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys = [];


      Object.keys(baseJson[key].items).forEach(function (key2) {
        if (key2 !== '__gohashid') {

          if (baseJson[key].items[key2].isDeleted === false
          ) {
            baseJsonKeys.push({
              id: baseJson[key].items[key2].id,
              name: baseJson[key].items[key2].name,
              isDeleted: baseJson[key].items[key2].isDeleted,
              color: baseJson[key].items[key2].color
            });
          }

        }
      });

      newJson.push({
        key: baseJson[key].key,
        items: baseJsonKeys
      });

      _this.setState({
        DiagramItems: newJson,
      })

    });
  }


  BindLeftTable = (id, _Items, _this) => {

    var baseJson = _Items;
    var newJson = [];
    var baseJsonKeys = [];

    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys = [];


      Object.keys(baseJson[key].items).forEach(function (key2) {
        if (key2 !== '__gohashid') {

          baseJsonKeys.push({
            id: baseJson[key].items[key2].id,
            name: baseJson[key].items[key2].name,
            isDeleted: baseJson[key].items[key2].isDeleted,
            color: baseJson[key].items[key2].color
          });
        }
      });

      newJson.push({
        key: baseJson[key].key,
        items: baseJsonKeys
      });

      _this.setState({
        Items: newJson,
      })

    });
  }


  BindDiagramById = (id, _this) => {

    var baseJson = this.state.Items;
    var newJson = [];
    var baseJsonKeys = [];

    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys = [];


      Object.keys(baseJson[key].items).forEach(function (key2) {
        if (key2 !== '__gohashid') {


          if (id === baseJson[key].items[key2].id) {

            if (baseJson[key].items[key2].isDeleted) {
              baseJsonKeys.push({
                id: baseJson[key].items[key2].id,
                name: baseJson[key].items[key2].name,
                isDeleted: baseJson[key].items[key2].isDeleted,
                color: baseJson[key].items[key2].color
              });
            }

          }
          else if (baseJson[key].items[key2].isDeleted === false) {
            baseJsonKeys.push({
              id: baseJson[key].items[key2].id,
              name: baseJson[key].items[key2].name,
              isDeleted: baseJson[key].items[key2].isDeleted,
              color: baseJson[key].items[key2].color
            });
          }
        }
      });

      newJson.push({
        key: baseJson[key].key,
        items: baseJsonKeys
      });

      _this.setState({
        DiagramItems: newJson,
      })

    });
  }

  BindLeftTableById = (id, _this) => {

    var baseJson = this.state.Items;
    var newJson = [];
    var baseJsonKeys = [];

    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys = [];
      var varisDeleted;


      Object.keys(baseJson[key].items).forEach(function (key2) {
        if (key2 !== '__gohashid') {
          varisDeleted = false;

          if (id === baseJson[key].items[key2].id) {

            varisDeleted = !baseJson[key].items[key2].isDeleted;
            baseJsonKeys.push({
              id: baseJson[key].items[key2].id,
              name: baseJson[key].items[key2].name,
              isDeleted: varisDeleted,
              color: baseJson[key].items[key2].color
            });
          }

          else {
            baseJsonKeys.push({
              id: baseJson[key].items[key2].id,
              name: baseJson[key].items[key2].name,
              isDeleted: baseJson[key].items[key2].isDeleted,
              color: baseJson[key].items[key2].color
            });
          }
        }
      });

      newJson.push({
        key: baseJson[key].key,
        items: baseJsonKeys
      });

      _this.setState({
        Items: newJson,
      })

    });
  }


  BindRelationById = (_table, _field, _isDeleted, _this) => {
    var baseJson = this.state.RelationItems;
    var baseJsonKeys = [];
    var baseJsonKeysDiagram = [];

    Object.keys(baseJson).forEach(function (key) {

      if (
        (_table === baseJson[key].from && baseJson[key].fromField === _field)
        ||
        (_table === baseJson[key].to && baseJson[key].toField === _field)
      ) {
        baseJsonKeys.push({
          from: baseJson[key].from,
          to: baseJson[key].to,
          text: baseJson[key].text,
          toText: baseJson[key].toText,
          fromField: baseJson[key].fromField,
          toField: baseJson[key].toField,
          isDeleted: !_isDeleted
        });
      }
      else {
        baseJsonKeys.push({
          from: baseJson[key].from,
          to: baseJson[key].to,
          text: baseJson[key].text,
          toText: baseJson[key].toText,
          fromField: baseJson[key].fromField,
          toField: baseJson[key].toField,
          isDeleted: baseJson[key].isDeleted
        });

        baseJsonKeysDiagram.push({
          from: baseJson[key].from,
          to: baseJson[key].to,
          text: baseJson[key].text,
          toText: baseJson[key].toText,
          fromField: baseJson[key].fromField,
          toField: baseJson[key].toField,
          isDeleted: baseJson[key].isDeleted
        });
      }

    });

    _this.setState({
      RelationItems: baseJsonKeys,
      RelationDiagramItems: baseJsonKeysDiagram
    })
  }

  BindRelationTable = (id, _Items, _this) => {

    var baseJson = _Items;
    var baseJsonKeys = [];
    var baseJsonKeysDiagram = [];

    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys.push({
        from: baseJson[key].from,
        to: baseJson[key].to,
        text: baseJson[key].text,
        toText: baseJson[key].toText,
        fromField: baseJson[key].fromField,
        toField: baseJson[key].toField,
        isDeleted: baseJson[key].isDeleted
      });

      if (baseJson[key].isDeleted === false) {
        baseJsonKeysDiagram.push({
          from: baseJson[key].from,
          to: baseJson[key].to,
          text: baseJson[key].text,
          toText: baseJson[key].toText,
          fromField: baseJson[key].fromField,
          toField: baseJson[key].toField,
          isDeleted: baseJson[key].isDeleted
        });
      }
    });


    _this.setState({
      RelationItems: baseJsonKeys,
      RelationDiagramItems: baseJsonKeysDiagram
    })
  }


  componentDidMount() {

    var jsonItems = [
      {
        key: "Products",
        items: [{ id: "1-1", name: "ProductID", isDeleted: true, iskey: true, figure: "Decision", color: "pink" },
        { id: "1-2", name: "ProductName", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue },
        { id: "1-3", name: "SupplierID", isDeleted: false, iskey: false, figure: "Decision", color: "purple" }]
      },
      {
        key: "Suppliers",
        items: [{ id: "2-1", name: "SupplierID", isDeleted: true, iskey: true, figure: "Decision", color: this.colors.red },
        { id: "2-2", name: "CompanyName", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue },
        { id: "2-3", name: "ContactName", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue },
        { id: "2-4", name: "Address", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue }]
      },
      {
        key: "Categories",
        items: [{ id: "3-1", name: "CategoryID", isDeleted: false, iskey: true, figure: "Decision", color: this.colors.red },
        { id: "3-2", name: "CategoryName", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue },
        { id: "3-3", name: "Description", isDeleted: false, iskey: false, figure: "Hexagon", color: this.colors.blue },
        { id: "3-4", name: "Picture", isDeleted: true, iskey: false, figure: "TriangleUp", color: this.colors.pink }]
      },
      {
        key: "Order Details",
        items: [{ id: "4-1", name: "OrderID", isDeleted: false, iskey: true, figure: "Decision", color: this.colors.red },
        { id: "4-2", name: "ProductID", isDeleted: false, iskey: true, figure: "Decision", color: this.colors.red },
        { id: "4-3", name: "UnitPrice", isDeleted: true, iskey: false, figure: "Circle", color: this.colors.green },
        { id: "4-4", name: "Quantity", isDeleted: true, iskey: false, figure: "Circle", color: this.colors.green },
        { id: "4-5", name: "Discount", isDeleted: false, iskey: false, figure: "Circle", color: this.colors.green },
        { id: "4-6", name: "Discount", isDeleted: false, iskey: false, figure: "Circle", color: this.colors.green },

        ]
      },
    ];

    var relationJsonItems = [
      { from: "Products", to: "Suppliers", text: "0..N", toText: "1", fromField: "SupplierID", toField: "SupplierID", isDeleted: false },
      { from: "Products", to: "Categories", text: "0..N", toText: "1", fromField: "SupplierID", toField: "CategoryID", isDeleted: false },
      { from: "Order Details", to: "Products", text: "0..N", toText: "1", fromField: "ProductID", toField: "ProductID", isDeleted: true }
    ];


    this.BindRelationTable(0, relationJsonItems, this);

    this.BindDiagram(0, jsonItems, this);
    this.BindLeftTable(0, jsonItems, this);




  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  AddNewRelation = () => {

    var baseJson = this.state.RelationItems;
    var baseJsonKeys = [];

    Object.keys(baseJson).forEach(function (key) {

      baseJsonKeys.push({
        from: baseJson[key].from,
        to: baseJson[key].to,
        text: baseJson[key].text,
        toText: baseJson[key].toText,
        fromField: baseJson[key].fromField,
        toField: baseJson[key].toField
      });
    });


    baseJsonKeys.push({
      from: this.state.relationFromDrp.split('.')[0],
      to: this.state.relationToDrp.split('.')[0],
      text: '0..N',
      toText: '1',
      fromField: this.state.relationFromDrp.split('.')[1],
      toField: this.state.relationToDrp.split('.')[1]
    });

    //baseJsonKeys = baseJson;


    this.setState({
      RelationItems: baseJsonKeys,
    })

    debugger;
  }

  render() {



    var { Items, DiagramItems, RelationItems, RelationDiagramItems } = this.state;
    return (



      <div >

        <div className="row">
          <div class="col-md-3" >
            {
              Items.map(Item => (
                React.createElement('div', { className: 'card' },
                  React.createElement('div', { className: 'card-header' },
                    React.createElement('h3', { className: 'card-title' }, Item.key),
                    React.createElement('div', { className: 'card-tools' },
                      React.createElement('button', { type: 'button', class: 'btn btn-tool', 'data-card-widget': 'collapse' },
                        React.createElement('i', { class: 'fas fa-minus' })
                      )

                    )

                  ),
                  React.createElement('div', { className: 'card-body p-0' },
                    React.createElement('ul', { className: 'nav nav-pills flex-column' },
                      Item.items.map(subItem =>

                        React.createElement('li',
                          {
                            Parent: Item.key,
                            IsDeleted: subItem.isDeleted,
                            key: subItem.id, onClick: this.handleDeleteFields.bind(this),

                            href: '#', className: subItem.isDeleted ? 'nav-link Decor-line' : 'nav-link'
                          }, subItem.name),

                      )
                    )
                  )
                )))
            }


          </div>

          <div class="col-md-9" >
            <ReactDiagram class="diagram-cover"
              initDiagram={initDiagram}
              divClassName='diagram-component'
              nodeDataArray={DiagramItems}
              linkDataArray={RelationDiagramItems}
              onModelChange={handleModelChange}
            />


            <div className="card">
              <div className="card-header">
                <h3 className="card-title">Relational Diagram</h3>
              </div>
              {/* /.card-header */}
              <div className="card-body">
                From:
                <select name="relationFromDrp" onChange={this.handleChange}>
                  {
                    Items.map(Item => (
                      Item.items.map(subItem =>
                        <option>{Item.key}.{subItem.name}</option>
                      )

                    ))

                  }
                </select>
                To:
                <select name="relationToDrp" onChange={this.handleChange}>
                  {
                    Items.map(Item => (
                      Item.items.map(subItem =>
                        <option>{Item.key}.{subItem.name}</option>
                      )

                    ))

                  }
                </select>
                <button class="btn btn-info btn-flat"
                  onClick={() => this.AddNewRelation()} >Add</button>
                <br />
                <table id="example1" className="table table-bordered table-striped">
                  <thead>
                    <tr>
                      <th>From Table</th>
                      <th>From Field</th>
                      <th>To Table</th>
                      <th>To Field</th>
                      <th>Status</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>

                    {
                      RelationItems.map(Item => (
                        <tr>
                          <td>
                            {Item.from}
                          </td>
                          <td>
                            {Item.fromField}
                          </td>
                          <td>
                            {Item.to}
                          </td>
                          <td>
                            {Item.toField}
                          </td>
                          <td>

                            {(() => {
                              switch (Item.isDeleted) {
                                case true:
                                  return <b>Deleted</b>;
                                default:
                                  return "";
                              }
                            })()}

                          </td>
                          <td>
                            {Icons.removeIcon}
                          </td>
                        </tr>
                      ))}

                  </tbody>
                </table>

              </div>
              {/* /.card-body */}
            </div>



          </div>
        </div>



      </div>
    );



    function GetRemoveIcon() {
      return <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-x" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" d="M11.854 4.146a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708-.708l7-7a.5.5 0 0 1 .708 0z" />
        <path fill-rule="evenodd" d="M4.146 4.146a.5.5 0 0 0 0 .708l7 7a.5.5 0 0 0 .708-.708l-7-7a.5.5 0 0 0-.708 0z" />
      </svg>;
    }


    // function ShowHtml(pureHtml) {

    //   return (
    //    <div  dangerouslySetInnerHTML={{__html: pureHtml}} />
    //   )
    // }
  }





}



function initDiagram() {
  const $ = go.GraphObject.make;
  // set your license key here before creating the diagram: go.Diagram.licenseKey = "...";

  var itemTempl =
    $(go.Panel, "Horizontal",
      $(go.Shape,
        { desiredSize: new go.Size(15, 15), strokeJoin: "round", strokeWidth: 3, stroke: null, margin: 2 },
        new go.Binding("figure", "figure"),
        new go.Binding("fill", "color"),
        new go.Binding("stroke", "color")),
      $(go.TextBlock,
        {
          stroke: "#333333",
          font: "bold 14px sans-serif"
        },
        new go.Binding("text", "name"))
    );
  const diagram =
    $(go.Diagram,
      {
        'allowHorizontalScroll': true,
        'undoManager.isEnabled': true,  // must be set to allow for model change listening
        // 'undoManager.maxHistoryLength': 0,  // uncomment disable undo/redo functionality
        'clickCreatingTool.archetypeNodeData': { text: 'new node', color: 'lightblue' },
        model: $(go.GraphLinksModel,
          {
            linkKeyProperty: 'key'  // IMPORTANT! must be defined for merges and data sync when using GraphLinksModel
          })
      });


  // define a simple Node template
  diagram.nodeTemplate =
    $(go.Node, "Auto",  // the whole node panel
      {
        selectionAdorned: true,
        resizable: true,
        layoutConditions: go.Part.LayoutStandard & ~go.Part.LayoutNodeSized,
        fromSpot: go.Spot.AllSides,
        toSpot: go.Spot.AllSides,
        isShadowed: true,
        shadowOffset: new go.Point(3, 3),
        shadowColor: "#C5C1AA"
      },
      new go.Binding("location", "location").makeTwoWay(),
      // whenever the PanelExpanderButton changes the visible property of the "LIST" panel,
      // clear out any desiredSize set by the ResizingTool.
      new go.Binding("desiredSize", "visible", function (v) { return new go.Size(NaN, NaN); }).ofObject("LIST"),
      // define the node's outer shape, which will surround the Table
      $(go.Shape, "RoundedRectangle",
        { fill: 'white', stroke: "#eeeeee", strokeWidth: 3 }),
      $(go.Panel, "Table",
        { margin: 8, stretch: go.GraphObject.Fill },
        $(go.RowColumnDefinition, { row: 0, sizing: go.RowColumnDefinition.None }),
        // the table header
        $(go.TextBlock,
          {
            row: 0, alignment: go.Spot.Center,
            margin: new go.Margin(0, 24, 0, 2),  // leave room for Button
            font: "bold 16px sans-serif"
          },
          new go.Binding("text", "key")),
        // the collapse/expand button
        $("PanelExpanderButton", "LIST",  // the name of the element whose visibility this button toggles
          { row: 0, alignment: go.Spot.TopRight }),
        // the list of Panels, each showing an attribute
        $(go.Panel, "Vertical",
          {
            name: "LIST",
            row: 1,
            padding: 3,
            alignment: go.Spot.TopLeft,
            defaultAlignment: go.Spot.Left,
            stretch: go.GraphObject.Horizontal,
            itemTemplate: itemTempl
          },
          new go.Binding("itemArray", "items"))
      )  // end Table Panel
    );

  return diagram;
}

function handleModelChange(changes) {
  // alert('GoJS model changed!');
}

export default databasediagram;