import React, { Component } from 'react';

class TargetVariable extends Component {
    state = {  }
    render() { 
        return ( 

            <div className="row">
                <div className="col-md-12">
                    <div class="card card-success">
                        <div class="card-header">
                            <h3 class="card-title">Table List</h3>
                        </div>
                        <div class="card-body">
                            <div class="form-group">
                                <select class="form-control" onChange={this.props.onChnageTableList}>
                                    {
                                        this.props.TableList.map((Item) =>
                                            <option value={Item.Title}>{Item.Title}</option>
                                        )
                                    }
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-12">
                    <div class="card card-secondary">
                        <div class="card-header">
                            <h3 class="card-title">Database Diagram</h3>
                        </div>
                        <div class="card-body">
                            <table id="example1" className="table table-bordered table-striped">
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th>Feature Title</th>
                                        <th>Target Variable</th>
                                    </tr>
                                </thead>
                                <tbody>


                                    {this.props.FeatureList.map((Item) => (
                                        <tr>
                                            <td>
                                                <input type="radio"  />

                                            </td>
                                            <td>
                                                {Item.name}
                                            </td>
                                            <td>
                                                {(() => {
                                                    switch (Item.isTargetValue) {
                                                        case true:
                                                            return showTick();
                                                        default:
                                                            return "";
                                                    }
                                                })()}
                                                
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            
                <div className="col-md-12" >
                    <button type="submit" class="btn btn-info"
                        onClick={this.props.onBindPkFkRelations}
                    >PK Fk Relations</button>
                    <br /><br />
                </div>
            </div>
         );

         function showTick() {
            return (


                <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-check" fill="#008000" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" d="M10.97 4.97a.75.75 0 0 1 1.071 1.05l-3.992 4.99a.75.75 0 0 1-1.08.02L4.324 8.384a.75.75 0 1 1 1.06-1.06l2.094 2.093 3.473-4.425a.236.236 0 0 1 .02-.022z" />
                </svg>
              
            );
          }
    }
}
 
export default TargetVariable;