import React, { Component } from 'react';
import Files from './files';
import axio from 'axios';
import TableRelations from './tableRelations';
import DbDiagram from './dbDiagram';
import UnitVarience from './unitVarience';
import OverallStatistic from '../setup/OverallStatistic';


class PrepareData extends Component {
    state = {
        fileNameInp: '',
        filePathInp: '',
        newTableNameInp: '',
        isFileError: false,
        fileErrorMessage: '',
        showMergeModal: false,
        relationMergeObj: [],
        isMergeModalError: false,
        mergeModalErrorMessage: '',
        UnitVarienceTableDrp: '',


        fileList: [],
        isLoadedFileList: false,
        JoinTableSourceList: [],
        JoinTableDestinationList: [],
        JointTableSourceStep0: [],
        JointTableDestinationStep0: [],
        JointTableStep1: [],
        JointTableStep2: [],
        JointTableStep3: [],
        SourceFeature: [],
        DestinationFeature: [],
        UnitVarienceTableList: [],
        UnitVarienceFeatureList: [],

        RelationItems: [],
        RelationDiagramItems: [],
        DiagramItemsPreventDelete: [],
        DiagramItems: [],
        PreDiagramItems: [],
        DiagramLeftTableItems: [],

        fileTabHeaderClass: 'nav-link',
        fileTabContentClass: 'tab-pane fade',

        unitVarienceHeaderClass: 'nav-link',
        unitVarienceContentClass: 'tab-pane fade',


        tableRelationTabHeaderClass: 'nav-link',
        tableRelationTabContentClass: 'tab-pane fade',

        dbDiagramTabHeaderClass: 'nav-link',
        dbDiagramTabContentClass: 'tab-pane fade',

        overallStatisticTabHeaderClass: 'nav-link',
        overallStatisticTabContentClass: 'tab-pane fade',
    }

    colors = {
        'red': '#be4b15',
        'green': '#52ce60',
        'blue': '#6ea5f8',
        'lightred': '#fd8852',
        'lightblue': '#afd4fe',
        'lightgreen': '#b9e986',
        'pink': '#faadc1',
        'purple': '#d689ff',
        'orange': '#fdb400',
    }

    // Handle All Input Change
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleCreateMerge = (e) => {
        const post = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", post)
            .then(res => {

                if (res.data.filter(x => x.key === this.state.newTableNameInp).length === 0) {
                    const postData = {
                        "source": this.state.relationMergeObj.from_Id,
                        "destination": this.state.relationMergeObj.to_Id,
                        "source_feature": this.state.relationMergeObj.fromField_Id,
                        "destination_feature": this.state.relationMergeObj.toField_Id,
                        "save_as": this.state.newTableNameInp,
                        "user_id": post.user_id
                    };

                    axio.post("http://brightbee.giize.com:8000/api/files/merge_datasets/", postData)
                        .then(res => {
                            this.setState({
                                showMergeModal: false
                            })
                            this.LoadBaseDataDiagram(this);
                            this.BindLeftTable();
                            this.BindRelationTable();
                        }
                        );
                }
                else {
                    this.setState({
                        isMergeModalError: true,
                        mergeModalErrorMessage: "There is a table same as your files, Please choose another name"
                    })

                }


            })

    }

    handleMergeTableModal = (e) => {

        this.setState({
            showMergeModal: true,
            relationMergeObj: e.row.data
        })

    }

    handleUploadFile = (e) => {

        if (!this.state.filePathInp.startsWith('https://drive.google.com')) {
            this.setState({
                isFileError: true,
                fileErrorMessage: "File path should start with https://drive.google.com"
            })
            return;
        }

        const user_id = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                debugger;

                if (res.data.filter(x => x.key === this.state.fileNameInp).length === 0) {
                    const postData = {
                        "file_name": this.state.fileNameInp,
                        "file_url": this.state.filePathInp,
                        "user_id": 111,
                        "folder_name": "root",
                        "date_created": new Date()
                    };

                    axio.post("http://brightbee.giize.com:8000/api/files/", postData)
                        .then(() =>

                            this.LoadAllUserFiles());
                }
                else {
                    this.setState({
                        isFileError: true,
                        fileErrorMessage: "There is a file same as your files, Please choose another name"
                    })

                }


            })

    }

    handleCreateNewTableRelation = (obj) => {
        const post = {
            from_file: this.state.PkFkrelationFromDrp,
            to_file: this.state.PkFkrelationToDrp,
            from_field: this.state.PkFkrelationFromDrpOpt,
            to_field: this.state.PkFkrelationToDrpOpt
        };

        axio.post("http://brightbee.giize.com:8000/api/relations/", post)
            .then(res => {
                const user_id = {
                    "user_id": 111
                };

                axio.post("http://brightbee.giize.com:8000/api/relations/userfiles_relations/", user_id)
                    .then(res => {
                        var newJson = [];

                        Object.keys(res.data).forEach(function (key) {
                            newJson.push({
                                from: res.data[key].fromFile,
                                to: res.data[key].toFile,
                                text: "",
                                toText: "",
                                fromField: res.data[key].fromField,
                                toField: res.data[key].toField
                            });
                        });

                        this.setState({
                            RelationItems: newJson,
                            RelationDiagramItems: newJson
                        });

                    }

                    );

            })
    }

    handleUnitVarience = async (e) => {

        const user_id = {
            user_id: 111
        };
        await axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                this.setState({
                    UnitVarienceTableList: res.data,
                    dbDiagramTabHeaderClass: 'nav-link',
                    dbDiagramTabContentClass: 'tab-pane fade',
                    unitVarienceHeaderClass: 'nav-link active',
                    unitVarienceContentClass: 'tab-pane fade show active'
                })
            })
    }

    handleUnitVarienceFeatureList = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            var tableList = [...this.state.UnitVarienceTableList];
            debugger;
            this.setState({
                UnitVarienceFeatureList: tableList.filter
                    (x => x.id === parseInt(this.state.UnitVarienceTableDrp))
            })
        });
    }

    handleDbDiagram = (e) => {

        const user_id = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                debugger;
                this.setState({
                    PreDiagramItems: res.data,
                    JoinTableSourceList: res.data.filter(
                        x => x.active === true
                            && x.pending === false
                    ),
                    JoinTableDestinationList: res.data.filter(
                        x => x.active === true
                            && x.pending === false
                    )
                }, () => {

                    const postData = {
                        "user_id": 111
                    };

                    axio.post("http://brightbee.giize.com:8000/api/relations/userfiles_relations/", postData)
                        .then(res2 =>

                            this.setState({
                                relationJsonItems: res2.data
                            }, () => {
                                debugger;

                                this.LoadBaseDataDiagram(this);
                                //this.BindDiagram(0, this.state.PreDiagramItems, this);
                                this.BindLeftTable();
                                this.BindRelationTable();
                                this.setState({

                                    fileTabHeaderClass: 'nav-link',
                                    fileTabContentClass: 'tab-pane fade',
                                    dbDiagramTabHeaderClass: 'nav-link active',
                                    dbDiagramTabContentClass: 'tab-pane fade show active'
                                })
                            }

                            )
                        );
                });

            })


    }


    handleOverallStatistic = (e) => {
        this.setState({

            tableRelationTabHeaderClass: 'nav-link',
            tableRelationTabContentClass: 'tab-pane fade',
            overallStatisticTabHeaderClass: 'nav-link active',
            overallStatisticTabContentClass: 'tab-pane fade show active'
        })
    }


    BindRelationTable = () => {

        const user_id = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/relations/userfiles_relations/", user_id)
            .then(res => {
                var newJson = [];

                Object.keys(res.data).forEach(function (key) {
                    newJson.push({
                        from: res.data[key].fromFile,
                        from_Id: res.data[key].from_file,
                        to: res.data[key].toFile,
                        to_Id: res.data[key].to_file,
                        text: "",
                        toText: "",
                        fromField: res.data[key].fromField,
                        fromField_Id: res.data[key].from_field,
                        toField: res.data[key].toField,
                        toField_Id: res.data[key].to_field
                    });
                });

                this.setState({
                    RelationItems: newJson,
                    RelationDiagramItems: newJson
                });

            }, () => {

            })
    }



    LoadBaseDataDiagram = (_this) => {

        const user_id = {
            user_id: 111
        };
        var baseJson = [];
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                this.setState({
                    DiagramItems: res.data
                }, () => {
                    // Load Data
                    baseJson = this.state.DiagramItems.filter(
                        x => x.active === true
                    );
                    var newJson = [];
                    Object.keys(baseJson).forEach(function (key) {

                        newJson.push({
                            key: baseJson[key].key,
                            items: baseJson[key].items.filter(
                                x => x.isDeleted === false
                            )
                        });

                    });

                    _this.setState({
                        DiagramItems: newJson,
                    })

                    this.BindLeftTable();
                });
            })

    }




    BindDiagramById = (id, parent, _this) => {

        const user_id = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {

                this.setState({
                    DiagramItemsPreventDelete: res.data
                }, () => {
                    var baseJson = this.state.DiagramItemsPreventDelete;
                    var currentTableArray = [...baseJson.filter(x => x.key === parent)]

                    if (currentTableArray[0].items.filter
                        (x => x.id === parseInt(id))[0].isDeleted === false &&
                        currentTableArray[0].items.filter(x => x.isDeleted === false).length < 2) {
                        alert("Less than one Item");
                    }
                    else {
                        const patchData = {
                            "inactive": !currentTableArray[0].items.filter(x => x.id === parseInt(id))[0].isDeleted,
                            "p_key": false
                        };
                        axio.patch("http://brightbee.giize.com:8000/api/fields/" + id + "/", patchData)
                            .then(() => {
                                this.LoadBaseDataDiagram(this);
                            })
                    }
                })
            });




    }

    BindLeftTable = () => {


        const user_id = {
            user_id: 111
        };
        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {

                res.data.sort(function (x, y) {
                    return (x.active === y.active) ? 0 : x.active ? -1 : 1;
                });

                this.setState({
                    DiagramLeftTableItems: res.data
                });

            })


    }




    BindRelationById = (_table, _field, _isDeleted, _this) => {
        var baseJson = this.state.RelationItems;
        var baseJsonKeys = [];
        var baseJsonKeysDiagram = [];

        Object.keys(baseJson).forEach(function (key) {

            if (
                (_table === baseJson[key].from && baseJson[key].fromField === _field)
                ||
                (_table === baseJson[key].to && baseJson[key].toField === _field)
            ) {
                baseJsonKeys.push({
                    from: baseJson[key].from,
                    to: baseJson[key].to,
                    text: baseJson[key].text,
                    toText: baseJson[key].toText,
                    fromField: baseJson[key].fromField,
                    toField: baseJson[key].toField,
                    isDeleted: !_isDeleted
                });
            }
            else {
                baseJsonKeys.push({
                    from: baseJson[key].from,
                    to: baseJson[key].to,
                    text: baseJson[key].text,
                    toText: baseJson[key].toText,
                    fromField: baseJson[key].fromField,
                    toField: baseJson[key].toField,
                    isDeleted: baseJson[key].isDeleted
                });

                baseJsonKeysDiagram.push({
                    from: baseJson[key].from,
                    to: baseJson[key].to,
                    text: baseJson[key].text,
                    toText: baseJson[key].toText,
                    fromField: baseJson[key].fromField,
                    toField: baseJson[key].toField,
                    isDeleted: baseJson[key].isDeleted
                });
            }

        });

        _this.setState({
            RelationItems: baseJsonKeys,
            RelationDiagramItems: baseJsonKeysDiagram
        })
    }


    handleDeleteFields = (obj) => {
        //this.BindLeftTableById(obj._targetInst.key, this);
        this.BindDiagramById(obj._targetInst.key, obj._targetInst.memoizedProps.Parent, this);
        // this.BindRelationById(obj._targetInst.memoizedProps.Parent,
        //     obj._targetInst.memoizedProps.children,
        //     obj._targetInst.memoizedProps.IsDeleted,
        //     this
        // )

    }




    handleTableRelation = async (e) => {

        const user_id = {
            user_id: 111
        };
        await axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                this.setState({
                    JoinTableSourceList: res.data,
                    JoinTableDestinationList: res.data,
                    dbDiagramTabHeaderClass: 'nav-link',
                    dbDiagramTabContentClass: 'tab-pane fade',
                    tableRelationTabHeaderClass: 'nav-link active',
                    tableRelationTabContentClass: 'tab-pane fade show active'
                })
            })
    }


    handleCascadeSourceTableFeature = (e) => {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            var source = [...this.state.fileList];

            this.setState({
                SourceFeature: source.filter
                    (x => x.id === parseInt(this.state.PkFkrelationFromDrp))
            })
        });
    }


    handleCascadeDestTableFeature = (e) => {

        this.setState({
            [e.target.name]: e.target.value
        }, () => {

            var dest = [...this.state.fileList];
            debugger;
            this.setState({
                DestinationFeature: dest.filter
                    (x => x.id === parseInt(this.state.PkFkrelationToDrp))
            })

        });
    }


    handleJointTable = (obj) => {

        let currentDateSet = [...this.state.fileList];
        debugger;
        currentDateSet.filter
            (x => x.id === parseInt(this.state.PkFkrelationFromDrp))[0].items
            .filter(x => x.id === parseInt(this.state.PkFkrelationFromDrpOpt))
        [0].iskey = true;

        currentDateSet.filter
            (x => x.id === parseInt(this.state.PkFkrelationToDrp))[0].items
            .filter(x => x.id === parseInt(this.state.PkFkrelationToDrpOpt))[0].iskey = true;



        var _Source = currentDateSet.filter
            (x => x.id === parseInt(this.state.PkFkrelationFromDrp));

        var _Destination = currentDateSet.filter
            (x => x.id === parseInt(this.state.PkFkrelationToDrp));

        this.setState({
            JointTableSourceStep0: _Source,
            JointTableDestinationStep0: _Destination,
            JointTableStep1: _Source.concat(_Destination)
        });
    }

    handleActiveTable = (obj) => {
        const postData = {
            active: true
        }
        axio.patch("http://brightbee.giize.com:8000/api/files/" +
            parseInt(obj._targetInst.key) + "/", postData)
            .then(() => {
                this.LoadBaseDataDiagram(this);
                this.BindLeftTable();
                this.BindRelationTable();
            })

    }

    LoadAllUserFiles() {
        const user_id = {
            user_id: 111
        };

        axio.post("http://brightbee.giize.com:8000/api/files/user_files/", user_id)
            .then(res => {
                debugger;
                this.setState({
                    fileList: res.data,
                    isLoadedFileList: true,
                    fileTabHeaderClass: 'nav-link active',
                    fileTabContentClass: 'tab-pane fade  show active',
                    isFileError: false,
                    fileErrorMessage: ''
                });
            })

    }

    //Component Did Mount

    componentDidMount() {
        //this.LoadAllUserFiles();
        this.handleUnitVarience();
    }


    render() {

        var { fileList, fileTabHeaderClass, fileTabContentClass,
            tableRelationTabHeaderClass, tableRelationTabContentClass,
            JoinTableSourceList, JoinTableDestinationList, JointTableSourceStep0,
            JointTableDestinationStep0, JointTableStep1, JointTableStep2, JointTableStep3,
            SourceFeature, DestinationFeature, dbDiagramTabHeaderClass, dbDiagramTabContentClass,
            RelationItems, RelationDiagramItems, DiagramItems, DiagramLeftTableItems,
            isFileError, fileErrorMessage, overallStatisticTabHeaderClass, overallStatisticTabContentClass,
            showMergeModal, isMergeModalError, mergeModalErrorMessage, unitVarienceHeaderClass,
            unitVarienceContentClass, UnitVarienceTableList, UnitVarienceFeatureList
        } = this.state;
        return (
            <div>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="card card-primary card-outline card-tabs">
                                <div className="card-header p-0 pt-1 border-bottom-0">
                                    <ul className="nav nav-tabs" id="custom-tabs-three-tab" role="tablist">
                                        <li className="nav-item">
                                            <a className={fileTabHeaderClass} href="#custom-tabs-three-home">1- Files</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className={dbDiagramTabHeaderClass} href="#custom-tabs-three-home">2- Diagram</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className={unitVarienceHeaderClass} href="#custom-tabs-three-home">3- Unit Varience</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className={tableRelationTabHeaderClass} href="#custom-tabs-three-profile" >3- Table Relations</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className={overallStatisticTabHeaderClass} href="#custom-tabs-three-profile" >4- Overall Statistic</a>
                                        </li>


                                    </ul>
                                </div>
                                <div className="card-body">
                                    <div className="tab-content" id="custom-tabs-three-tabContent">
                                        <div className={fileTabContentClass} id="custom-tabs-three-home" role="tabpanel" aria-labelledby="custom-tabs-three-home-tab">
                                            <Files
                                                handleChange={this.handleChange}
                                                onUploadFile={this.handleUploadFile}
                                                fileList={fileList}
                                                isFileError={isFileError}
                                                fileErrorMessage={fileErrorMessage}
                                            />
                                            <div className="row" >
                                                <div className="col-md-12" >
                                                    <button type="submit" class="btn btn-info"
                                                        onClick={this.handleDbDiagram}
                                                    >Go to Diagram</button>
                                                    <br /><br />
                                                </div>
                                            </div>
                                        </div>

                                        <div className={dbDiagramTabContentClass} id="custom-tabs-three-home" role="tabpanel" aria-labelledby="custom-tabs-three-home-tab">
                                            <DbDiagram
                                                RelationItems={RelationItems}
                                                RelationDiagramItems={RelationDiagramItems}
                                                DiagramItems={DiagramItems}
                                                DiagramLeftTableItems={DiagramLeftTableItems}
                                                handleDeleteFields={this.handleDeleteFields}


                                                TableFeatureListSrc={JoinTableSourceList}
                                                SourceFeature={SourceFeature}
                                                TableFeatureListDest={JoinTableDestinationList}
                                                DestinationFeature={DestinationFeature}
                                                onOptionChange={this.handleChange}
                                                onOptionSourceChange={this.handleCascadeSourceTableFeature}
                                                onOptionDestChange={this.handleCascadeDestTableFeature}
                                                onBindCreateNewTableRelation={this.handleCreateNewTableRelation}

                                                handleChange={this.handleChange}
                                                onCreateMerge={this.handleCreateMerge}
                                                MergeTableModal={this.handleMergeTableModal}
                                                showMergeModal={showMergeModal}
                                                isMergeModalError={isMergeModalError}
                                                mergeModalErrorMessage={mergeModalErrorMessage}

                                                handleActiveTable={this.handleActiveTable}
                                            />
                                            <div className="row" >
                                                <div className="col-md-12" >
                                                    <button type="submit" class="btn btn-info"
                                                        onClick={this.handleUnitVarience}
                                                    >Go to Unit Varience</button>
                                                    <br /><br />
                                                </div>
                                            </div>
                                        </div>

                                        <div className={unitVarienceContentClass} id="custom-tabs-three-home" role="tabpanel" aria-labelledby="custom-tabs-three-home-tab">
                                            <UnitVarience
                                                UnitVarienceTableList={UnitVarienceTableList}
                                                onOptionVarienceTableChange={this.handleUnitVarienceFeatureList}
                                                UnitVarienceFeatureList={UnitVarienceFeatureList}
                                            />
                                        </div>

                                        <div className={tableRelationTabContentClass} id="custom-tabs-three-profile" role="tabpanel" aria-labelledby="custom-tabs-three-profile-tab">
                                            <TableRelations
                                                TableFeatureListSrc={JoinTableSourceList}
                                                TableFeatureListDest={JoinTableDestinationList}
                                                onOptionSourceChange={this.handleCascadeSourceTableFeature}
                                                onOptionDestChange={this.handleCascadeDestTableFeature}
                                                onOptionChange={this.handleChange}
                                                onBindJoinTable={this.handleJointTable}
                                                onBindClearTable={this.handleClearTable}
                                                onSaveNewTable={this.handleSaveNewTable}

                                                JointTableSourceStep0={JointTableSourceStep0}
                                                JointTableDestinationStep0={JointTableDestinationStep0}
                                                JointTableStep1={JointTableStep1}
                                                JointTableStep2={JointTableStep2}
                                                JointTableStep3={JointTableStep3}

                                                SourceFeature={SourceFeature}
                                                DestinationFeature={DestinationFeature}
                                            />
                                            <div className="row" >
                                                <div className="col-md-12" >
                                                    <button type="submit" class="btn btn-info"
                                                        onClick={this.handleOverallStatistic}
                                                    >Go to Overal Statistic</button>
                                                    <br /><br />
                                                </div>
                                            </div>
                                        </div>
                                        <div className={overallStatisticTabContentClass} id="custom-tabs-three-profile" role="tabpanel" aria-labelledby="custom-tabs-three-profile-tab">
                                            <OverallStatistic />
                                        </div>

                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );


    }


}

export default PrepareData;